var parallaxEffect=require("./core/parallax.js");
require("./core/main-nav.js");

if($(window).width()>780){//只有在屏幕大小小于540的情况下才执行
    bannerEffect();
}else{
    parallaxEffect();
}
//banner区域效果实现
function bannerEffect(){
    var bannerCon=$('#main-banner__con'),
        bannerItem=bannerCon.find('.main-banner__item');
    $(document).on('mousemove',function(e){
        var winW=$(window).width(),
            conH=bannerCon.height(),
            centerPoint={x:parseInt(winW/2),y:parseInt(conH/2)},
            pageX=e.pageX,
            pageY=e.pageY,
            difX=centerPoint.x-pageX,
            difY=centerPoint.y-pageY,
            ratio0=0.06,
            ratio1=0.04,
            ratio2=0.02,
            ratio3=0.01;
        if(winW<780 || pageY>conH){//如果是在小屏上，或者鼠标位置脱离banner区
            return;
        }
        bannerItem.eq(2).css({'transform':'translate('+parseInt(difX*ratio0)+'px,'+parseInt(difY*ratio0)+'px)'});
        bannerItem.eq(1).css({'transform':'translate('+parseInt(difX*ratio1)+'px,'+parseInt(difY*ratio1)+'px)'});
        bannerItem.eq(3).css({'transform':'translate('+parseInt(difX*ratio2)+'px,'+parseInt(difY*ratio2)+'px)'});
        bannerItem.eq(0).css({'transform':'translate('+parseInt(difX*ratio3)+'px,'+parseInt(difY*ratio3)+'px)'});
    })
}



