function parallaxEffect(){
    var parallaxList=$('[data-parallax-ratio]');
    $(window).on('scroll',function(){
        var scrollT=$(window).scrollTop(),
            winH=$(window).height();
        parallaxList.each(function(){
            var offsetT=$(this).offset().top,
                difZ=offsetT-scrollT,
                conH=$(this).height(),
                ratioZ=$(this).attr('data-parallax-ratio');
            if(difZ<=parseInt(winH/2) && (conH+offsetT)>=scrollT){
                $(this).css({'transform':'translateY('+parseInt(difZ*ratioZ)+'px)'});
            }
        })
    }) 
}
module.exports=parallaxEffect;


